.body {
  background-color: gray;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 400; }

.app {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 100px; }

.search__input {
  border: none;
  height: 61px;
  width: 500px;
  padding: 0 12px;
  font-size: 48px;
  font-weight: 400; }
  .search__input:focus {
    outline: none;
    box-shadow: 2px -2px 6px 0px rgba(0, 122, 0, 0.4); }

.search__dropdown {
  list-style: none;
  padding: 0;
  margin: 0;
  display: none; }

.search.open .search__dropdown {
  display: block; }

.search__item {
  width: 496px;
  background-color: #b2b3b2;
  padding: 0 12px;
  height: 44px;
  font-size: 30px;
  border: 2px solid black; }
  .search__item:hover {
    background-color: #65CDF9;
    cursor: pointer; }

.search__subscribtion {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 520px;
  background-color: #E27BEB;
  margin-top: 40px; }

.repository {
  padding: 12px;
  font-size: 24px;
  border: 1px solid black;
  display: flex;
  flex-direction: row;
  justify-content: space-between; }
  .repository__btn-delete {
    background: none;
    border: none;
    transition-duration: 0.3s; }
    .repository__btn-delete:hover {
      transition-duration: 0.3s;
      transform: scale(1.2);
      cursor: pointer; }
  .repository__btn-icon {
    width: 80px; }
